.sign-up-container1 {
  width: 100%;
  height: calc(100vh - 60px);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: var(--dl-color-theme-neutral-light);
  overflow: hidden;
  position: relative;
}
 
.sign-up-max-width {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}
 
.sign-up-sign-up-image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  z-index: 0;
  pointer-events: none;
}
 
.sign-up-form {
  position: relative;
  z-index: 2;
  background: rgba(255,255,255,0.95);
  gap: var(--dl-space-space-twounits);
  width: 100%;
  max-width: 500px;
  display: flex;
  align-items: center;
  border-radius: 25px;
  flex-direction: column;
  justify-content: flex-end;
  padding: var(--dl-space-space-twounits);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px;
}
 
.sign-up-title-root {
  gap: var(--dl-space-space-halfunit);
  display: flex;
  align-items: center;
  flex-direction: column;
}
 
.sign-up-text10 {
  text-align: center;
  font-size: var(--dl-font-size-large);
}
 
.sign-up-container2 {
  gap: var(--dl-space-space-unit);
  width: 100%;
  display: flex;
  box-sizing: border-box;
  align-items: center;
  flex-direction: column;
  justify-content: flex-end;
}
 
.sign-up-button1,
.sign-up-button2 {
  width: 100%;
  padding: var(--dl-space-space-halfunit);
  margin-bottom: var(--dl-space-space-unit);
  border-radius: 25px;
  font-size: var(--dl-font-size-small);
  cursor: pointer;
  transition: all 0.3s ease;
}

.sign-up-button1 {
  width: 100%;
  padding: var(--dl-space-space-halfunit);
  margin-bottom: var(--dl-space-space-unit);
  border-radius: 25px;
  font-size: var(--dl-font-size-small);
  cursor: pointer;
  transition: all 0.3s ease;
  background-color: var(--dl-color-theme-primary1);
  color: #fff;
  border: 1px solid var(--dl-color-theme-primary1);
  text-decoration: none;
  display: block;
}

.sign-up-button1:hover {
  background-color: var(--dl-color-theme-primary2);
  transform: translateY(-2px);
  text-decoration: none;
}

.sign-up-button1 a {
  text-decoration: none;
  color: inherit;
  display: block;
  width: 100%;
  height: 100%;
}

.sign-up-button1 a:hover {
  text-decoration: none;
}

.sign-up-button2 {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--dl-color-theme-primary1);
  background-color: transparent;
  color: var(--dl-color-theme-primary1);
}

.sign-up-button2:hover {
  background-color: var(--dl-color-theme-primary2);
  color: #fff;
  transform: translateY(-2px);
}
 
.sign-up-text12 {
  text-align: center;
}
 
.sign-up-icon1 {
  width: 24px;
  height: 24px;
}
 
.sign-up-text13 {
  text-align: center;
}
 
.sign-up-text14 {
  font-size: var(--dl-font-size-small);
  text-align: center;
}
 
.sign-up-text15 {
  font-size: var(--dl-font-size-small);
}
 
.sign-up-text18 {
  font-size: var(--dl-font-size-small);
}
 
.sign-up-navlink {
  text-decoration: none;
  color: var(--dl-color-theme-primary1);
  transition: color 0.3s ease;
}

.sign-up-navlink:hover {
  color: var(--dl-color-theme-primary2);
  text-decoration: underline;
}
 
.sign-up-text20 {
  display: inline-block;
  font-size: 32px;
}
 
.sign-up-text21 {
  display: inline-block;
}
 
.sign-up-text22 {
  display: inline-block;
}
 
.sign-up-text23 {
  display: inline-block;
}
 
.sign-up-text28 {
  display: inline-block;
  font-size: var(--dl-font-size-large);
}

.sign-up-text27 {
  display: inline-block;
  font-size: var(--dl-font-size-small);
}

.sign-up-text25 {
  display: inline-block;
  font-size: var(--dl-font-size-small);
}

.sign-up-text26 {
  display: inline-block;
  font-size: var(--dl-font-size-small);
}
 
@media(max-width: 991px) {
  .sign-up-max-width {
    padding: 10px;
  }
  
  .sign-up-form {
    margin: 10px;
    padding: var(--dl-space-space-unit);
  }
}
 
@media(max-width: 479px) {
  .sign-up-container1 {
    height: calc(100vh - 60px);
  }
  
  .sign-up-max-width {
    padding: 5px;
  }
  
  .sign-up-form {
    margin: 5px;
    padding: var(--dl-space-space-halfunit);
  }
  .sign-up-text10 {
    text-align: center;
  }
  .sign-up-button1 {
    width: 100%;
  }
  .sign-up-button2 {
    width: 100%;
  }
  .sign-up-text18 {
    text-align: center;
  }
}

.sign-up-form input {
  width: 100%;
  padding: var(--dl-space-space-halfunit);
  border: var(--dl-border-width-thin) solid var(--dl-color-theme-primary1);
  border-radius: 25px;
  background-color: transparent;
  font-size: var(--dl-font-size-small);
  margin-bottom: var(--dl-space-space-unit);
}


.terms-checkbox {
  display: flex;
  align-items: center;
  margin: 10px 0;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
}

.terms-checkbox input[type="checkbox"] {
  margin-right: 10px;
  width: 18px;
  height: 18px;
  cursor: pointer;
}

.terms-checkbox label {
  font-size: 14px;
  color: #666;
  cursor: pointer;
}

.terms-checkbox .sign-up-navlink {
  color: #007bff;
  text-decoration: none;
}

.terms-checkbox .sign-up-navlink:hover {
  text-decoration: underline;
}

.navbar-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 4px;
}

.navbar-button:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.navbar-icon {
  width: 24px;
  height: 24px;
  fill: currentColor;
}

.button {
  color: var(--dl-color-theme-primary1);
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
}

@media(max-width: 479px) {
  .navbar-button {
    padding: 6px;
  }
  
  .navbar-icon {
    width: 20px;
    height: 20px;
  }
}
